const aboutService = {
  // --
  list(params) {
    //return axios.post('/about/list')

    return new Promise((resolve, reject) => {
			uni.request({
			  url: '/about/list',
			  data: params,
			  method: "POST",
			  header: {
			    'content-type': 'application/x-www-form-urlencoded'
			  },
				success: res => {
					resolve(res);
				},
				fail: err => {
					console.log(err)
				}
			});
		})
  }
  /*
  // post
  add (params) {
    return axios.post(`${base.sq}/accesstoken`, qs.stringify(params))
  }
  */
}
export default aboutService
